<template>
  <div class="center resultDetails">
    <div class="center_breadcrumb" style="font-size:14px; height: 84px; background-color: transparent;line-height:84px;">
      <!-- <Crumb /> -->
      <span style="color: #696969;cursor: pointer;" @click="back">OBE成绩分析</span> > <span style="color: #03B187;">成绩详情</span>
    </div>
    <div class="center_content">
      <div class="title">{{$route.query.courseName}}成绩详情
          <div style="width:120px;height:28px;border:1px solid rgba(176,249,224,1);border-radius:14px;font-size:14px;line-height:28px;float: right;cursor: pointer; position: absolute; top: 50%; left: 30px; transform: translateY(-50%); color: #fff" @click="back">
            <SvgIcon icon-class="iconfanhui"></SvgIcon>  返回列表</div>
      </div>
      <div class="courseTarget">
        <div class="targetTitle">课程目标</div>
        <div class="targetBody">
          <div class="targetItem" v-for="(item, index) in courseGoalTable" :key="index">
            <div class="index">
              <img v-if="(index + 1) % 4 === 1 || (index + 1) % 4 === 2" src="@/assets/img/bianhao.png" alt="">
              <img v-if="(index + 1) % 4 === 3 || (index + 1) % 4 === 0" src="@/assets/img/bianhao2.png" alt="">
              <span>{{index + 1}}</span>
            </div>
            <div class="itemTitle">{{item.name}}</div>
            <div class="itemContent" :title="item.content">{{item.content}}</div>
          </div>
        </div>
      </div>
      <div class="result">
        <div class="resultTitle">成绩分析</div>
        <div id="charts"></div>
      </div>
      <div class="result">
        <div class="resultTitle">达成度分析</div>
        <div id="charts1"></div>
      </div>
      <div class="tableContent">
        <div class="table1">
          <el-table
            :data="courseScore"
            style="width: 100%"
            stripe>
            <el-table-column
              prop="goalName"
              label="课程目标">
            </el-table-column>
            <el-table-column
              prop="total"
              label="设定分值">
            </el-table-column>
            <el-table-column
              prop="score"
              label="实际得分">
            </el-table-column>
            <el-table-column
              prop="targetScore"
              label="期望分值">
            </el-table-column>
            <el-table-column
              prop="avgScore"
              label="班级平均得分">
            </el-table-column>
            <el-table-column
              prop="ratio"
              label="直接达成度">
            </el-table-column>
            <el-table-column
              prop="prop"
              label="间接达成度">
            </el-table-column>
            <el-table-column
              prop="targetRatio"
              label="期望达成度">
            </el-table-column>
            <el-table-column
              prop="avgRatio"
              label="班级平均达成度">
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import studentApi from "@/api/studentApi";
//import SeriesBar from "../components/Echarts/SeriesBar";
import echarts from 'echarts'
export default {
  name: "resultDetails",
  components: {},
  data() {
    return {
      pageName: "成绩详情",
      pageTitleArr: [],
      id: this.$route.query.id,
      seriesBarData: {},
      seriesBarDataTwo: {},
      courseGoalTable: [],
      courseScore: [],
      courseScoreTable: [
        {
          name: "分值设定",
          type: "total",
          list: []
        },
        {
          name: "实际得分",
          type: "score",
          list: []
        },
        {
          name: "达成度",
          type: "ratio",
          list: []
        },
        {
          name: "班级平均得分",
          type: "avgScore",
          list: []
        },
        {
          name: "平均达成度",
          type: "avgRatio",
          list: []
        },
        {
          name: "目标分值",
          type: "targetScore",
          list: []
        },
        {
          name: "目标达成度",
          type: "targetRatio",
          list: []
        }
      ]
    };
  },
  created() {
    this.pageName = `${this.$route.query.courseName}成绩详情`;
    this.pageTitleArr = this.$route.query.pageTitle;
  },
  mounted() {
    this.getCourseTargetPoint();
    this.getCourseScore();
  },
  methods: {
    back() {
      this.$router.push({
        name: 'resultQuery'
      })
    },
    //课程目标
    getCourseTargetPoint() {
      const url = studentApi.getCourseTargetPoint;
      const postData = {
        courseId: this.id
      };
      this.$get(url, postData).then(res => {
        if (res.length !== 0) {
          this.courseGoalTable = res;
        }
      });
    },
    //成绩单
    getCourseScore() {
      const that = this
      const url = studentApi.getCourseScore;
      const postData = {
        courseId: this.id
      };
      this.$get(url, postData).then(res => {
        if (res.length !== 0) {
          this.courseScore = res;
          this.courseScoreTable.forEach(item => {
            res.forEach(vo => {
              if (vo.hasOwnProperty(item.type)) {
                item.list.push(vo[item.type]);
              }
            });
          });
          let xAxis = []
          let total = []
          let score = []
          let targetScore = []
          let avgScore = []

          let ratio = []
          let tmpVal = []
          let targetRatio = []
          let avgRatio = []
          res.forEach(item => {
            xAxis.push(item.goalName)
            total.push(item.total)
            score.push(item.score)
            targetScore.push(item.targetScore)
            avgScore.push(item.avgScore)

            ratio.push(item.ratio)
            tmpVal.push(0)
            targetRatio.push(item.targetRatio)
            avgRatio.push(item.avgRatio)
          })
          that.initCharts(xAxis, total, score, targetScore, avgScore);
          this.initCharts1(xAxis, ratio, targetRatio, tmpVal, avgRatio)
        }
      });
    },
    initCharts(xAxis, total, score, targetScore, avgScore) {
      const charts = echarts.init(document.getElementById('charts'))
      let option = {
        tooltip : {
          trigger: 'axis',
          axisPointer : {
            type : 'shadow'
          }
        },
        grid: {
          left: '5%',
          right: '5%'
        },
        legend: {
          data:['实际得分','期望分值','班级平均得分','设定分值'],
          top: '5%',
          right: '5%'
        },
        toolbox: {
          show : true,
          feature : {
            saveAsImage : {show: true}
          },
          top: '3%'
        },
        calculable : true,
        xAxis : [
          {
            type : 'category',
            data : xAxis,
            axisLine:{
              show:false
            },
            axisTick:{
              show:false
            }
          }
        ],
        yAxis : [
          {
            type : 'value',
            axisLine:{
              show:false
            },
            axisTick:{
              show:false
            }
          }
        ],
        series: [
          {
            name:'实际得分',
            type:'bar',
            barGap: '0',
            data:score,
            barMaxWidth: 30,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#03B187'
                  },
                  {
                    offset: 1,
                    color: '#8EE9CA'
                  }
                ]),
              }
            }
          },
          {
            name:'期望分值',
            type:'bar',
            barGap: '0',
            data:targetScore,
            barMaxWidth: 30,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#5AB3FF'
                  },
                  {
                    offset: 1,
                    color: '#B3DCFF'
                  }
                ]),
              }
            }
          },
          {
            name:'班级平均得分',
            type:'bar',
            barGap: '0',
            data:avgScore,
            barMaxWidth: 30,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#D5CFFF'
                  },
                  {
                    offset: 1,
                    color: '#D5CFFF'
                  }
                ]),
              }
            }
          },
          {
            name:'设定分值',
            type:'bar',
            barGap: '0',
            data:total,
            barMaxWidth: 30,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#FFA351'
                  },
                  {
                    offset: 1,
                    color: '#FFE098'
                  }
                ]),
              }
            }
          }
        ]
      }
      charts.setOption(option)
      window.addEventListener('resize', function(){
        charts.resize()
      })
    },
    initCharts1(xAxis, val, targetVal, tmpVal, classVal) {
      const charts1 = echarts.init(document.getElementById('charts1'))
      let option = {
        tooltip : {
          trigger: 'axis',
          axisPointer : {
            type : 'shadow'
          }
        },
        grid: {
          left: '5%',
          right: '5%'
        },
        legend: {
          data:['直接达成度','间接达成度','期望达成度','班级平均达成度'],
          top: '5%',
          right: '5%'
        },
        toolbox: {
          show : true,
          feature : {
            saveAsImage : {show: true}
          },
          top: '3%'
        },
        calculable : true,
        xAxis : [
          {
            type : 'category',
            data : xAxis,
            axisLine:{
              show:false
            },
            axisTick:{
              show:false
            }
          }
        ],
        yAxis : [
          {
            type : 'value',
            axisLine:{
              show:false
            },
            axisTick:{
              show:false
            }
          }
        ],
        series: [
          {
            name:'直接达成度',
            type:'bar',
            barGap: '0',
            data:val,
            barMaxWidth: 30,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#03B187'
                  },
                  {
                    offset: 1,
                    color: '#8EE9CA'
                  }
                ]),
              }
            }
          },
          {
            name:'间接达成度',
            type:'bar',
            barGap: '0',
            barMaxWidth: 30,
            data:tmpVal,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#FFA351'
                  },
                  {
                    offset: 1,
                    color: '#FFE098'
                  }
                ]),
              }
            }
          },
          {
            name:'期望达成度',
            type:'bar',
            barGap: '0',
            barMaxWidth: 30,
            data:targetVal,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#5AB3FF'
                  },
                  {
                    offset: 1,
                    color: '#B3DCFF'
                  }
                ]),
              }
            }
          },
          {
            name:'班级平均达成度',
            type:'bar',
            barGap: '0',
            data:classVal,
            barMaxWidth: 30,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#D5CFFF'
                  },
                  {
                    offset: 1,
                    color: '#D5CFFF'
                  }
                ]),
              }
            }
          }
        ]
      }
      charts1.setOption(option)
      window.addEventListener('resize', function(){
        charts1.resize()
      })
    },
    // echartInitData(arr) {
    //   //柱状图图一
    //   const echartData = {
    //     legendData: ["设定分值", "实际得分", "班级平均得分", "目标得分"],
    //     xAxisData: [],
    //     seriesData: [
    //       {
    //         name: "设定分值",
    //         barGap: "5%", //柱状图间距
    //         data: []
    //       },
    //       {
    //         name: "实际得分",
    //         barGap: "5%",
    //         data: []
    //       },
    //       {
    //         name: "班级平均得分",
    //         barGap: "5%",
    //         data: []
    //       },
    //       {
    //         name: "目标得分",
    //         barGap: "5%",
    //         data: []
    //       }
    //     ]
    //   };
    //   const setScore = [];
    //   const getScore = [];
    //   const meanScore = [];
    //   const goalScore = [];
    //   //柱状图二
    //   const echartDataTwo = {
    //     legendData: ["达成度", "平均达成", "目标达成"],
    //     xAxisData: [],
    //     seriesData: [
    //       {
    //         name: "达成度",
    //         barGap: "5%", //柱状图间距
    //         data: []
    //       },
    //       {
    //         name: "平均达成",
    //         barGap: "5%",
    //         data: []
    //       },
    //       {
    //         name: "目标达成",
    //         barGap: "5%",
    //         data: []
    //       }
    //     ]
    //   };
    //   const ratioArr = [];
    //   const avgRatioArr = [];
    //   const targetRatioArr = [];

    //   arr.forEach(item => {
    //     if (item.hasOwnProperty("goalName")) {
    //       echartData.xAxisData.push(item.goalName);
    //       echartDataTwo.xAxisData.push(item.goalName);
    //     }
    //     if (item.hasOwnProperty("total")) {
    //       setScore.push(item.total);
    //     }
    //     if (item.hasOwnProperty("score")) {
    //       getScore.push(item.score);
    //     }
    //     if (item.hasOwnProperty("avgScore")) {
    //       meanScore.push(item.avgScore);
    //     }
    //     if (item.hasOwnProperty("targetScore")) {
    //       goalScore.push(item.targetScore);
    //     }
    //     //柱状图二
    //     if (item.hasOwnProperty("ratio")) {
    //       ratioArr.push(item.ratio);
    //     }
    //     if (item.hasOwnProperty("avgRatio")) {
    //       avgRatioArr.push(item.avgRatio);
    //     }
    //     if (item.hasOwnProperty("targetRatio")) {
    //       targetRatioArr.push(item.targetRatio);
    //     }
    //   });
    //   echartData.seriesData[0].data = setScore;
    //   echartData.seriesData[1].data = getScore;
    //   echartData.seriesData[2].data = meanScore;
    //   echartData.seriesData[3].data = goalScore;

    //   echartDataTwo.seriesData[0].data = ratioArr;
    //   echartDataTwo.seriesData[1].data = avgRatioArr;
    //   echartDataTwo.seriesData[2].data = targetRatioArr;

    //   this.seriesBarData = echartData;
    //   this.seriesBarDataTwo = echartDataTwo;
    // }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.resultDetails {
  padding-bottom: 50px;
  .title {
    height: 85px;
    line-height: 85px;
    text-align: center;
    color: #fff;
    font-size:24px;
    letter-spacing: 0.2em;
    background-image: url('../../assets/img/bg-OBE.png');
    background-size: 100% 100%;
    position: relative;
  }
  .courseTarget {
    padding: 30px 30px 0;
    border-radius: 10px;
    background-color: #fff;
    margin-top: 20px;
    .targetTitle {
      margin-bottom: 30px;
    }
    .targetBody {
      overflow: hidden;
      .targetItem {
        box-sizing: border-box;
        padding: 0 70px 0 0;
        float: left;
        width: 50%;
        overflow: hidden;
        margin-bottom: 42px;
        height: 84px;
        .index {
          width: 44px;
          height: 44px;
          text-align: center;
          line-height: 44px;
          float: left;
          position: relative;
          span {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%)
          }
        }
        .itemTitle {
          margin-left: 65px;
          color: #323232;
          font-size:14px;
          margin-bottom: 10px;
        }
        .itemContent {
          margin-left: 65px;
          line-height: 2;
          color: #777;
          font-size:14px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
      }
    }
  }
  .result {
    padding: 30px;
    background-color: #fff;
    border-radius: 10px;
    margin-top: 20px;
    height: 300px;
    .resultTitle{
      font-size:20px;
      font-weight:bold;
      color:#323232;
    }
    #charts,
    #charts1 {
      width: 100%;
      height: 100%;
    }
  }
  .tableContent {
    margin-top: 20px;
    overflow: hidden;
    .table1 {
      width: 100%;
      padding-bottom: 20px;
      border-radius: 10px;
      background-color: #fff;
      .el-table__footer-wrapper{
        margin-top:0;
      }
    }
  }
}
</style>



